@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;600;700&display=swap);
:root{
  --dark-grey:hsl(0, 0%, 63%);
  --black:hsl(0, 0%, 0%);
  --white: hsl(0, 0%, 100%);
  --very-dark-grey:hsl(0, 0%, 27%);

}

body{
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-family: 'Spartan', sans-serif;
}

*{
 
}

h1,h2,p,a{
  margin: 0;
  padding: 0;
  margin-bottom: 28px;
}
li, ul{
  margin: 0;
  padding: 0;
}

h1{
  font-size: 2rem;
}
p{
  color: hsl(0, 0%, 63%);
  color: var(--dark-grey);
  line-height: 1.6;
}
a{
  text-decoration: none;
  margin: 0;
  padding: 0;
}

.App{
  position: relative;
}

.main-description{
    padding: 0 16%;
    /* background: cornflowerblue; */
    position: relative;
}
.main-description-a{
    text-decoration: none;
    letter-spacing: 5px;
    color: var(--black);
    transition: 200ms ease-in-out;
}
.main-description-a:hover{
    color: var(--dark-grey);
    fill: var(--dark-grey);
}
.main-description-a:hover svg{
    fill: var(--dark-grey);
}


@media (max-width: 1440px){
    .main-description{
        padding: 48px 24px;
    }
    
}
svg{
    fill: var(--black);
    transition: 200ms ease-in-out;
}
.main-image{
    position: relative;
}
.main-image-img{
    width: 100%;
}
.main-descriptio-buttons-nav{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    bottom: 0;
    right: 0;
}

@media (max-width: 1440px){
    .main-descriptio-buttons-nav{
        top: auto;
        right: 0;
        left: auto;
        bottom: 0;
        left: auto;
    }
}


.button{
    background: var(--black);
    border: none;
    width: 80px;
    height: 80px;
}
.button:hover{
    cursor: pointer;
    background: var(--dark-grey);
}
.button:active{
    cursor: pointer;
    background: var(--black);
}

.main{
    position: relative;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    object-fit: cover;
  
}
.main div{
    display: flex;
    justify-content: center;
    flex-direction: column;
}
@media (max-width: 1440px){
    .main{
        grid-template-columns: 100vw;
        grid-template-rows: auto auto;
    }
}
.about-image{
    width: 100%;
    object-fit: cover;
}
.about-text{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 5%;
}
.about{
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto;
}

@media (max-width: 1440px){
    .about{
        grid-template-columns: 100vw;
        grid-template-rows: auto auto;
    }
}
.menu{
    position: absolute;
    top: 48px;
    left: 24px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: var(--white);
    z-index: 100;
}

.menu h1{
    margin: 0;
    margin-right: 32px;
}

.menu .menu-expanded{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    list-style: none;
}

.menu li{
    margin-right: 8px;
    padding: 8px;
}

.menu li a { 
    color: var(--white);
    position: relative;
}
.menu .menu-expanded li a::after{
    content: "";
    width: 20px;
    height: 2px;
    background-color: transparent;
    position: absolute;
    bottom: -8px;
    left: 0;
    right: 0;
    z-index: 1;
    margin: auto;
    transition: 200ms ease-in-out;
}
.menu .menu-expanded li a:hover::after{
    background-color: white;
}

.menu-mobile-icon{
    display: none;
}
.menu-mobile-icon button{
    background: none;
    border: none;
    outline: none;
}

.none{
    display: none;
}

@media (max-width:1000px){
    .menu{
        top: 20px;
        left: 20px;
    }
    .menu-mobile-icon{
        display: block;
        margin-right: 28vw;
    }
    .menu .menu-expanded{
        display: none;
    }
    
}


.menu-mobile-expanded {
    background: rgba(0, 0, 0, 0.473);
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 200;
}
.menu-mobile-expanded nav{
    background:  var(--white);
    height: 10vh;
    display: block;
    display: flex;
    justify-content:  space-between;
    align-items: center;
}
.menu-mobile-expanded ul{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    list-style: none;
    padding: 16px 8px;
}
.menu-mobile-expanded ul li{
    margin-right: 16px;
}
.menu-mobile-expanded ul li a{
    color: black;
    font-weight: bold;
}
.menu-mobile-expanded button{
    background: none;
    border: none;
    outline: none;
    margin-left: 8px;
}
